import { useToast } from 'vue-toastification/composition'
import ToastificationContent from '@/@core/components/toastification/ToastificationContent.vue'
import UserService from '@/services/UserService'
import { reactive, ref, computed } from '@vue/composition-api'

const useUser = ctx => {
  const toast = useToast()
  const isSaving = ref(false)
  const isLoading = ref(false)
  const blank = {
    id: '',
    name: '',
    lastName: '',
    email: '',
    password: '',
    isActive: true,
  }
  const states = reactive({
    email: {
      isInvalid: false,
      message: '',
    },
  })
  const isSidebarActive = ref(false)

  const user = reactive(blank)

  const resetData = () => {
    setTimeout(() => {
      user.id = ''
      user.name = ''
      user.isActive = true
      user.lastName = ''
      user.email = ''
      user.password = ''

      states.email.isInvalid = false
      states.email.message = ''
    }, 1000)
  }

  const saveNew = async () => {
    isSaving.value = true
    try {
      const { id, ...rest } = user
      await UserService.create(rest)
      isSaving.value = false
      isSidebarActive.value = false
      setTimeout(() => {
        resetData()
      }, 1000)
      toast({
        component: ToastificationContent,
        props: {
          title: 'Registrado correctamente!!',
          icon: 'CheckCircleIcon',
          variant: 'success',
        },
      })
      return true
    } catch (error) {
      isSaving.value = false
      if (error.response.data.statusCode === 400) {
        states.email.isInvalid = true
        states.email.message = error.response.data.message
      }
      toast({
        component: ToastificationContent,
        props: {
          title: 'Error al Guardar',
          icon: 'AlertTriangleIcon',
          variant: 'danger',
        },
      })
      throw error
    }
  }

  const edit = async id => {
    isLoading.value = true
    try {
      const response = await UserService.getById(id)
      const {
        name,
        isActive,
        lastName,
        email,
      } = response.data

      user.id = response.data.id
      user.name = name
      user.isActive = isActive
      user.lastName = lastName
      user.email = email

      isLoading.value = false
      isSidebarActive.value = true
    } catch (error) {
      isLoading.value = false
      toast({
        component: ToastificationContent,
        props: {
          title: 'Error al Consultar registro',
          icon: 'AlertTriangleIcon',
          variant: 'danger',
        },
      })
      throw error
    }
  }

  const update = async () => {
    isSaving.value = true
    try {
      const { id, ...rest } = user
      await UserService.update(user.id, rest)
      isSaving.value = false
      isSidebarActive.value = false
      resetData()
      toast({
        component: ToastificationContent,
        props: {
          title: 'Actualizado Correctamente!',
          icon: 'CheckCircleIcon',
          variant: 'success',
        },
      })
    } catch (error) {
      isSaving.value = false
      if (error.response.data.statusCode === 400) {
        states.email.isInvalid = true
        states.email.message = error.response.data.message
      }
      toast({
        component: ToastificationContent,
        props: {
          title: 'Error al Actualizar',
          icon: 'AlertTriangleIcon',
          variant: 'danger',
        },
      })
      throw error
    }
  }

  const passwordToggleIcon = computed(() => (ctx.parent.$children[0].passwordFieldType === 'password' ? 'EyeOffIcon' : 'EyeIcon'))

  const save = async () => {
    if (user.id !== '') {
      await update()
    } else {
      await saveNew()
    }
  }

  return {
    isSaving,
    isSidebarActive,
    states,
    user,
    // methods
    save,
    resetData,
    edit,
    update,
    passwordToggleIcon,
  }
}
export default useUser
