<template>
  <b-sidebar
    id="sidebar"
    sidebar-class="sidebar-lg"
    :visible="isSidebarActive"
    bg-variant="white"
    shadow
    backdrop
    no-header
    right
    @hidden="()=> {
      resetForm(),
      resetData()
    }"
    @change="(val) => isSidebarActive = val"
  >
    <template #default="{ hide }">
      <div
        class="d-flex justify-content-between align-items-center content-sidebar-header px-2 py-1"
      >
        <h5 class="mb-0">
          {{ user.id ? "Editar" : "Registrar" }} Usuario
        </h5>

        <feather-icon
          class="ml-1 cursor-pointer"
          icon="XIcon"
          size="16"
          @click="hide"
        />
      </div>

      <!-- BODY -->
      <validation-observer
        #default="{ handleSubmit }"
        ref="refFormObserver"
      >
        <b-form
          ref="form"
          class="p-2"
          @submit.prevent="handleSubmit(onSubmit)"
        >
          <b-alert
            v-if="states.email.isInvalid"
            show
            variant="danger"
          >
            <div class="alert-body">
              <feather-icon
                class="mr-25"
                icon="AlertCircleIcon"
              />
              <span class="ml-25">{{ states.email.message }}</span>
            </div>
          </b-alert>
          <validation-provider
            #default="validationContext"
            name="name"
            rules="required"
          >
            <b-form-group
              :state="getValidationState(validationContext)"
              label="Nombre"
              label-for="name"
            >
              <b-form-input
                id="name"
                v-model="user.name"
                placeholder="nombre"
              />
              <b-form-invalid-feedback
                :state="getValidationState(validationContext)"
              >
                {{ validationContext.errors[0] }}
              </b-form-invalid-feedback>
            </b-form-group>
          </validation-provider>

          <validation-provider
            #default="validationContext"
            name="lastName"
            rules="required"
          >
            <b-form-group
              :state="getValidationState(validationContext)"
              label="Apellidos"
              label-for="lastName"
            >
              <b-form-input
                id="lastName"
                v-model="user.lastName"
                placeholder="Apellidos"
              />
              <b-form-invalid-feedback
                :state="getValidationState(validationContext)"
              >
                {{ validationContext.errors[0] }}
              </b-form-invalid-feedback>
            </b-form-group>
          </validation-provider>

          <!-- email -->
          <b-form-group
            label="Email"
            label-for="email"
          >
            <validation-provider
              #default="{ errors }"
              name="email"
              rules="required|email"
            >
              <b-form-input
                id="login-email"
                v-model="user.email"
                :state="errors.length > 0 ? false:null"
                name="email"
                placeholder="john@example.com"
              />
              <small class="text-danger">{{ errors[0] }}</small>
            </validation-provider>
          </b-form-group>

          <!-- Password -->
          <b-form-group>
            <div class="d-flex justify-content-between">
              <label for="password">Contraseña</label>
            </div>
            <validation-provider
              #default="{ errors }"
              name="password"
              :rules="user.id ? '' : 'required|min:8'"
            >
              <b-input-group
                class="input-group-merge"
                :class="errors.length > 0 ? 'is-invalid' : null"
              >
                <b-form-input
                  id="password"
                  v-model="user.password"
                  :state="errors.length > 0 ? false : null"
                  class="form-control-merge"
                  :type="passwordFieldType"
                  name="login-password"
                  placeholder="············"
                />
                <b-input-group-append is-text>
                  <feather-icon
                    class="cursor-pointer"
                    :icon="passwordToggleIcon"
                    @click="togglePasswordVisibility"
                  />
                </b-input-group-append>
              </b-input-group>
              <!-- <small class="text-danger">{{ errors[0] }}</small> -->
              <small class="text-danger">{{ errors[0] }}</small>
            </validation-provider>
          </b-form-group>

          <b-form-group
            label="Activo"
            label-for="status"
          >
            <b-form-checkbox
              v-model="user.isActive"
              class="custom-control-success"
              name="status"
              switch
            />
          </b-form-group>
          <b-button
            v-ripple.400="'rgba(255, 255, 255, 0.15)'"
            variant="primary"
            class="mr-2"
            type="submit"
            block
          >
            <feather-icon
              icon="SaveIcon"
              class="mr-50 mt-25"
            />
            <b-spinner
              v-if="isSaving"
              small
            />
            <span class="align-middle ml-1">
              {{ user.id ? "Actualizar" : "Registrar " }}
            </span>
          </b-button>
        </b-form>
      </validation-observer>
    </template>
  </b-sidebar>
</template>

<script>
import {
  BAlert,
  BSidebar,
  BForm,
  BInputGroup,
  BInputGroupAppend,
  BFormCheckbox,
  BFormGroup,
  BFormInput,
  BFormInvalidFeedback,
  BButton,
  BSpinner,
} from 'bootstrap-vue'
import { ValidationProvider, ValidationObserver, localize } from 'vee-validate'
import { togglePasswordVisibility } from '@core/mixins/ui/forms'
import { required } from '@validations'
import formValidation from '@core/comp-functions/forms/form-validation'
import Ripple from 'vue-ripple-directive'
import useUser from '../composables/useUser'

localize({
  en: {
    messages: {
      // generic rule messages...
    },
    fields: {
      name: {
        required: 'Introduce un nombre',
      },
      lastName: {
        required: 'Introduce los apellidos',
      },
      email: {
        required: 'El email es obligatorio',
        // email:''
      },
      password: {
        required: 'La contraseña es obligatoria!',
      },
    },
  },
})

export default {
  components: {
    BAlert,
    BSpinner,
    BSidebar,
    BForm,
    BInputGroup,
    BInputGroupAppend,
    BFormCheckbox,
    BFormGroup,
    BFormInput,
    BFormInvalidFeedback,
    BButton,

    // Form Validation
    ValidationProvider,
    ValidationObserver,
  },
  directives: {
    Ripple,
  },
  mixins: [togglePasswordVisibility],
  setup(__, ctx) {
    const resetuserData = () => ctx.emit('reset-data')

    const { refFormObserver, getValidationState, resetForm } = formValidation(resetuserData)

    const {
      user,
      isSaving,
      isSidebarActive,
      save,
      edit,
      resetData,
      states,
      passwordToggleIcon,
    } = useUser(ctx)

    const onSubmit = () => {
      save().then(() => ctx.emit('reload'))
    }

    const open = () => {
      isSidebarActive.value = true
    }

    return {
      user,
      isSaving,
      isSidebarActive,
      states,
      onSubmit,
      refFormObserver,
      getValidationState,
      resetForm,
      open,
      edit,
      resetData,

      required,
      passwordToggleIcon,
    }
  },
}
</script>

<style lang="scss">
#add-new-user-sidebar {
  .vs__dropdown-menu {
    max-height: 200px !important;
  }
}
</style>
