<template>
  <div>
    <UsersTable
      ref="usersTable"
      @handle-edit="(id) => $refs.sidebarUser.edit(id)"
    >
      <template v-slot:plus>
        <b-button
          variant="primary"
          @click="$refs.sidebarUser.open()"
        >
          <span class="text-nowrap">Registrar Nuevo</span>
        </b-button>
      </template>
    </UsersTable>
    <SidebarUser
      ref="sidebarUser"
      @reload="$refs.usersTable.fetchList()"
    />
  </div>
</template>

<script>
import { ref } from '@vue/composition-api'
import { BButton } from 'bootstrap-vue'
import UsersTable from './components/UsersTable.vue'
import SidebarUser from './components/SidebarUser.vue'

export default {
  components: {
    BButton,
    UsersTable,
    SidebarUser,
  },
  setup() {
    const sidebarUser = ref()
    const dusersTable = ref()
    return {
      dusersTable,
      sidebarUser,
    }
  },
}
</script>

